@import url('https://fonts.googleapis.com/css2?family=Acme&display=swap');
:root{
  --background: linear-gradient(to bottom right, rgb(17, 3, 3), rgb(15, 2, 2));
  --heading-color: #ffffff;
  --search-background: linear-gradient(to left, rgb(202, 0, 0), rgb(168, 0, 0));
}

*{
  margin: 0;
  padding: 0;
}
body{
  background: var(--background);
  width: 100vw;
  overflow-x: hidden;
}

.App {
  font-family: 'Acme', sans-serif;
  letter-spacing: 0.5px;
}

a{
  text-decoration: none;
}

.search-section{
  text-align: center;
  margin: 20px 10px;
  color: var(--heading-color);
}

.main-heading{
  margin: 20px 10px;
}

.main-heading a{
  color: var(--heading-color);
}

.searchbar input{
  transition: 0.3s;
  width: 300px;
  height: 40px;
  padding-left: 15px;
  background: var(--search-background);
  border: none;
  outline: none;
  caret-color: rgb(202, 0, 0);
  border-radius:  20px;
  box-sizing: border-box;
}

.searchbar input::placeholder{
  text-shadow: 0 0 1px var(--heading-color);
  font-size: 16px;
  font-family: 'Acme', sans-serif;
  color: var(--heading-color);
}

.searchbar{
  position: relative;
  display: flex;
  align-items: center;
  width: 300px;
  border-radius: 20px;
  margin: auto;
}

.searchbar input:focus, .searchbar:hover > input, .more button:hover{
  transition: 0.3s;
  color: #ffffff;
  background: none;
  border-top: 1px solid rgb(202, 0, 0);
  border-bottom: 1px solid rgb(138, 0, 0);
}

.searchbar input:focus ~ span, .searchbar:hover > span{
  transition: 0.3s;
  background: var(--search-background);
  color: var(--heading-color);
  box-shadow: none;
}

.searchbar span{
  transition: 0.3s;
  position: absolute;
  right: 5px;
  color: rgb(202, 0, 0);
  display: flex;
  align-items: center;
  justify-content: center;
  background: var(--heading-color);
  width: 30px;
  height: 30px;
  font-size: 16px;
  border-radius: 50%;
  box-shadow: 0 0 5px 5px rgb(175, 28, 28);
}

.universities{
  display: flex;
  align-items: center;
  justify-content: space-around;
  flex-wrap: wrap;
  margin: 30px 20px;
}

.more{
  text-align: center;
  margin: auto;
  margin-bottom: 100px;
}

.more button{
  transition: 0.3s;
  letter-spacing: 0.5px;
  cursor: pointer;
  color: var(--heading-color);
  font-family: 'Acme', sans-serif;
  font-size: 16px;
  width: 180px;
  height: 40px;
  background: var(--search-background);
  border: none;
  outline: none;
  border-radius:  20px;
}

.more button:hover span svg {
  color: rgb(202, 0, 0);
}

.more button span svg {
  margin-left: 5px;
}

.more span{
  display: flex;
  align-items: center;
  justify-content: center;
}

.nof{
  color: var(--heading-color);
  text-align: center;
}

.circle{
  margin: auto;
  height: 40px;
  width: 40px;
  border-radius: 50%;
  border-right: 2px solid transparent;
  border-top: 2px solid rgb(202, 0, 0);
  animation: loading 2s linear infinite;
}

@keyframes loading{
  from{
    transform: rotate(0);
  }
  to{
    transform: rotate(360deg);
  }
}