.university{
    width: 100%;
    max-width: 500px;
    transition: 0.2s;
    text-align: center;
    position: relative;
    margin: 5px 10px;
    perspective: 1000px;
}

.university:hover{
    transition: 0.2s;
    transform: scale(1.1);
}

.university:hover .university-name{
    transition: 0.2s;
    background: #fff;
}

.university:hover .university-name a{
    transition: 0.2s;
    color: rgb(202, 0, 0);
}

.extra-uni1, .extra-uni2{
    width: calc(100%);
    height: calc(100%);
    position: absolute;
    background: linear-gradient(to left, rgb(202, 0, 0), rgb(168, 0, 0));
    top: 50%;
    left: 50%;
    z-index: -1;
}

.extra-uni2{
    transform: translate(-50%, -50%) skewY(0.3deg) skewX(5deg);
}

.extra-uni1{
    transform: translate(-50%, -50%) skewY(-0.3deg) skewX(-5deg);
}

.university-name{
    background: linear-gradient(to bottom, rgb(27, 24, 24), rgb(26, 21, 21));
    padding: 10px;
    z-index: 1;
}

.university-name a{
    color: white;
}