@import url(https://fonts.googleapis.com/css2?family=Acme&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root{
  --background: linear-gradient(to bottom right, rgb(17, 3, 3), rgb(15, 2, 2));
  --heading-color: #ffffff;
  --search-background: linear-gradient(to left, rgb(202, 0, 0), rgb(168, 0, 0));
}

*{
  margin: 0;
  padding: 0;
}
body{
  background: linear-gradient(to bottom right, rgb(17, 3, 3), rgb(15, 2, 2));
  background: var(--background);
  width: 100vw;
  overflow-x: hidden;
}

.App {
  font-family: 'Acme', sans-serif;
  letter-spacing: 0.5px;
}

a{
  text-decoration: none;
}

.search-section{
  text-align: center;
  margin: 20px 10px;
  color: #ffffff;
  color: var(--heading-color);
}

.main-heading{
  margin: 20px 10px;
}

.main-heading a{
  color: #ffffff;
  color: var(--heading-color);
}

.searchbar input{
  transition: 0.3s;
  width: 300px;
  height: 40px;
  padding-left: 15px;
  background: linear-gradient(to left, rgb(202, 0, 0), rgb(168, 0, 0));
  background: var(--search-background);
  border: none;
  outline: none;
  caret-color: rgb(202, 0, 0);
  border-radius:  20px;
  box-sizing: border-box;
}

.searchbar input:-ms-input-placeholder{
  text-shadow: 0 0 1px #ffffff;
  text-shadow: 0 0 1px var(--heading-color);
  font-size: 16px;
  font-family: 'Acme', sans-serif;
  color: #ffffff;
  color: var(--heading-color);
}

.searchbar input::placeholder{
  text-shadow: 0 0 1px #ffffff;
  text-shadow: 0 0 1px var(--heading-color);
  font-size: 16px;
  font-family: 'Acme', sans-serif;
  color: #ffffff;
  color: var(--heading-color);
}

.searchbar{
  position: relative;
  display: flex;
  align-items: center;
  width: 300px;
  border-radius: 20px;
  margin: auto;
}

.searchbar input:focus, .searchbar:hover > input, .more button:hover{
  transition: 0.3s;
  color: #ffffff;
  background: none;
  border-top: 1px solid rgb(202, 0, 0);
  border-bottom: 1px solid rgb(138, 0, 0);
}

.searchbar input:focus ~ span, .searchbar:hover > span{
  transition: 0.3s;
  background: linear-gradient(to left, rgb(202, 0, 0), rgb(168, 0, 0));
  background: var(--search-background);
  color: #ffffff;
  color: var(--heading-color);
  box-shadow: none;
}

.searchbar span{
  transition: 0.3s;
  position: absolute;
  right: 5px;
  color: rgb(202, 0, 0);
  display: flex;
  align-items: center;
  justify-content: center;
  background: #ffffff;
  background: var(--heading-color);
  width: 30px;
  height: 30px;
  font-size: 16px;
  border-radius: 50%;
  box-shadow: 0 0 5px 5px rgb(175, 28, 28);
}

.universities{
  display: flex;
  align-items: center;
  justify-content: space-around;
  flex-wrap: wrap;
  margin: 30px 20px;
}

.more{
  text-align: center;
  margin: auto;
  margin-bottom: 100px;
}

.more button{
  transition: 0.3s;
  letter-spacing: 0.5px;
  cursor: pointer;
  color: #ffffff;
  color: var(--heading-color);
  font-family: 'Acme', sans-serif;
  font-size: 16px;
  width: 180px;
  height: 40px;
  background: linear-gradient(to left, rgb(202, 0, 0), rgb(168, 0, 0));
  background: var(--search-background);
  border: none;
  outline: none;
  border-radius:  20px;
}

.more button:hover span svg {
  color: rgb(202, 0, 0);
}

.more button span svg {
  margin-left: 5px;
}

.more span{
  display: flex;
  align-items: center;
  justify-content: center;
}

.nof{
  color: #ffffff;
  color: var(--heading-color);
  text-align: center;
}

.circle{
  margin: auto;
  height: 40px;
  width: 40px;
  border-radius: 50%;
  border-right: 2px solid transparent;
  border-top: 2px solid rgb(202, 0, 0);
  animation: loading 2s linear infinite;
}

@keyframes loading{
  from{
    transform: rotate(0);
  }
  to{
    transform: rotate(360deg);
  }
}
.university{
    width: 100%;
    max-width: 500px;
    transition: 0.2s;
    text-align: center;
    position: relative;
    margin: 5px 10px;
    perspective: 1000px;
}

.university:hover{
    transition: 0.2s;
    transform: scale(1.1);
}

.university:hover .university-name{
    transition: 0.2s;
    background: #fff;
}

.university:hover .university-name a{
    transition: 0.2s;
    color: rgb(202, 0, 0);
}

.extra-uni1, .extra-uni2{
    width: calc(100%);
    height: calc(100%);
    position: absolute;
    background: linear-gradient(to left, rgb(202, 0, 0), rgb(168, 0, 0));
    top: 50%;
    left: 50%;
    z-index: -1;
}

.extra-uni2{
    transform: translate(-50%, -50%) skewY(0.3deg) skewX(5deg);
}

.extra-uni1{
    transform: translate(-50%, -50%) skewY(-0.3deg) skewX(-5deg);
}

.university-name{
    background: linear-gradient(to bottom, rgb(27, 24, 24), rgb(26, 21, 21));
    padding: 10px;
    z-index: 1;
}

.university-name a{
    color: white;
}
